.skills-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    background-color: black;
    padding-top: 100px;
    padding-bottom: 250px;
    /* animation: disappear_left both linear;
    animation-timeline: view();
    animation-range: entry 120% cover 100%; */
}

.skillset {
    display: flex;
    flex-flow: row;
    gap: 50px;
    justify-content: center;
    flex-wrap: wrap;
}

h3 {
    width: 120px;
    text-align: center;
}

.icons {
    width: 500px;
}

.icons a {
    text-decoration: none;
}

@keyframes disappear_left {
    from {
      opacity: 1;
      transform: translateY(0px);
      filter: blur(0px);
    }
    to {
      opacity: 0;
      transform: translateY(-200px);
      filter: blur(3px);
    }
  }