.projects-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    background-color: black;
    padding-top: 100px;
    /* animation: appear 1.5s ease-in-out;
    animation-timeline: view();
    animation-range: entry 15% cover 15%; */
    padding-bottom: 50px;
}

h1 {
  font-size: xx-large;
}

.card-container {
  display: flex;
  flex-direction: column;
  width: 80%;
  align-content: space-around;
  gap: 100px;
}

.card {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 50px;
    align-items: center;
}

.showcase {
  background-color: blue;
  width: auto;
  height: 600px;
  animation: disappear_showcase both linear;
  animation-timeline: view();
  animation-range: entry 130% cover 100%;
}


.sticky--1st {
    top: 150px; /* Override the initial top for the first div */
    box-shadow: 0 2px 30px rgba(0, 0, 100, 1); 
  }
  
  .sticky--2nd {
    top: 200px;
    box-shadow: 0 2px 30px rgba(150, 10, 100, 1); 
    background-color: black;
  }
  
  .sticky--3rd {
    top: 250px;
  }
  
  .sticky--4th {
    top: 300px;
  }
  
  .sticky--5th {
    top: 350px;
  }

  @keyframes appear {
    from {
      opacity: 0;
      transform: translateY(-150px)
    }
    to {
      opacity: 1;
      transform: translateY(0)
    }
  }

  @keyframes disappear_showcase {
    from {
      opacity: 1;
      transform: translateY(0px);
      filter: blur(0px);
    }
    to {
      opacity: 0;
      transform: translateY(-200px);
      filter: blur(3px);
    }
  }


  #disclaimer {
    font-size: medium;
    text-align: center;
    margin: 100px;
  }