.description {
    max-width: 550px;
    /* background-color: aqua; */
    padding: 10px;
    box-shadow: 0 2px 15px rgba(100, 100, 100, 1); 
    border-radius: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    /* animation: disappear_left both linear;
    animation-timeline: view();
    animation-range: entry 130% cover 100%; */
  }

  .card-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* background-color: blueviolet; */
  }

  .title {
    text-align: center;
    font-size: x-large;
    /* background-color: blue; */
    margin-bottom: -10px;
  }
  .year {
    text-align: center;
    font-size: large;
    /* background-color: aqua; */
  }

  .keywords_container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
    /* background-color: antiquewhite; */
  }

  .keyword {
    border-radius: 10px;
    border-color: lightgreen;
    color: lightgreen;
    border-style: solid;
    border-width: 1px;
    padding: 5px;
    font-size: medium;
  }

  .text {
    text-align: center;
    font-size: medium;
  }

  .text a {
    color: whitesmoke
  }

  @keyframes disappear_left {
    from {
      opacity: 1;
      transform: translateY(0px);
      filter: blur(0px);
    }
    to {
      opacity: 0;
      transform: translateY(80px);
      filter: blur(3px);
    }
  }
