.contact-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: black;
    animation: appear 1.5s ease-in-out;
    animation-timeline: view();
    animation-range: entry 0% cover 30%;
    padding-bottom: 200px;
}

h1 {
  font-size: xx-large;
}

a {
    color: antiquewhite;
}

@keyframes appear {
    from {
      opacity: 0;
      transform: translateY(-150px)
    }
    to {
      opacity: 1;
      transform: translateY(0)
    }
  }

  