.navigation-wrapper {
    position: fixed;
    width: 100%;
    margin-top: 0px;
    padding-top: 20px;
    padding-bottom: 0px;
    background-color: black;
    z-index: 1;
    box-shadow: 0 20px 30px rgba(0, 0, 0, 1); 
}

.navigation-bar {
    width: 100%;
    margin: 0 auto;
    background-color: #212121;
    color: white;
    display: flex;
    justify-content: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-radius: 50px; /* Add rounded corners */
    max-width: 800px;
    font-family: 'Inconsolata', monospace;
    font-size: medium;
    height: 30px; 
    z-index: 1;
  }
  
  .navigation-bar ul {
    display: flex;
    justify-content: space-around;
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .navigation-bar li {
    display: flex;
    justify-content: center;
    align-items: center;
    color: inherit; /* Inherit color from parent */
    text-decoration: none;
    text-align: center;
    transition: all 0.3s ease-in-out;
  }
  
  .navigation-bar li:hover {
    cursor:pointer;
    text-shadow: 
    0 0 5px #106835,  /* Inner green glow */
    0 0 15px #179e46,  /* Mid green glow */
    0 0 30px #2fe960;   /* Outer green glow */
  }

  .active-link {
    text-shadow: 
      0 0 5px #106835,  /* Inner green glow */
      0 0 15px #179e46,  /* Mid green glow */
      0 0 30px #2fe960;   /* Outer green glow */
  }

  .navigation-bar li a:focus {
    cursor:pointer;
    color: white; /* Green hover effect */
    text-shadow: 
      0 0 10px #2ECC71,  /* Inner green glow */
      0 0 15px #1DB954,  /* Mid green glow */
      0 0 17px #17A63C;   /* Outer green glow */
  }
  
  