.about-section {
    padding-top: 180px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-right: 10%;
    padding-left: 10%;
    padding-bottom: 150px;
    flex-wrap: wrap-reverse;
    width: 80%;
    gap: 50px;
    animation: about-section-appear 1.5s ease-in-out forwards;
}

.right_box{
  width: 40%;
  min-height: 100px;
  border-radius: 20px;
  padding-left: -100px;
  display: flex;
  flex-direction: column;
  gap: 50px;
}

#portrait {
  border-radius: 50px;
  align-self: center;
  animation: about-appear 1.5s ease-in-out forwards;
}

.typewriter {
    font-size:xx-large;
    height: 150px;
    align-self: baseline;
}

.about {
    border-width: 1px;
    padding: 40px;
    box-shadow: 0 2px 15px rgba(100, 100, 100, 1); 
    border-radius: 50px;
    min-width: 45%; /* Adjust width as needed */
    max-width: 600px;
    animation: disappear_left both linear;
    animation-timeline: view();
    animation-range: entry 150% cover 100%;
    animation: glow 1s ease-in-out infinite alternate;
  }

.about a {
    color: antiquewhite;
}
  
  .about__title {
    font-size: 1.4em;
    margin-bottom: 10px;
    text-align: left;
  }
  
  .about__subtitle {
    font-size: 1.1em;
    margin-bottom: 15px;
    text-align: left;
    color: lightgreen
  }
  
  .about__text {
    font-size: 1em;
    line-height: 1.5;
  }

  @keyframes glow {
    from {
      box-shadow: 0 2px 20px rgb(11, 102, 38); 
    }
    to {
      box-shadow: 0 2px 25px rgb(24, 214, 87); 
    }
  }
  
  @keyframes about-section-appear {
    from {
      filter: blur(5px);
      opacity: 0;
    }
    to {
      filter: blur(0px);
      opacity: 1;
    }
  }

  @keyframes disappear_left {
    from {
      opacity: 1;
      transform: translateY(0px);
      filter: blur(0px);
    }
    to {
      opacity: 0;
      transform: translateY(-200px);
      filter: blur(3px);
    }
  }